.product-type {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  box-sizing: border-box;
  width: 100%;
  margin: 0 auto;
  padding: 0;
  list-style: none;
  z-index: 1;

  @media (max-width: @mobile-only) {
    flex-direction: column;
  }

  @media (min-width: @tablet-width) {
    padding: 0 50px 1px 50px;
  }

  &::after {
    @media (min-width: @tablet-width) {
      content: "";
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 40px;
      min-width: 770px;
      background-repeat: no-repeat;
      background-position: center bottom;
      background-image: url("../img/zigzag-fill-desktop.svg");
      background-size: 100% auto;
      z-index: 1;
    }

    @media (min-width: @desktop-width) {
      min-width: 1250px;
      background-size: 100% 34px;
    }
  }
}

.product-type__item {
  flex-grow: 1;
  min-width: 50%;
}

.product-type__link {
  position: relative;
  box-sizing: border-box;
  display: block;
  padding: 24px 100px 31px 30px;
  font-size: 19px;
  line-height: 24px;
  color: @white;

  @media (min-width: @tablet-width) {
    padding: 26px 100px 44px 36px;
    font-size: 20px;
    line-height: 30px;
  }

  @media (min-width: @desktop-width) {
    padding: 26px 100px 48px 37px;
    font-size: 20px;
    line-height: 31px;
  }

  &::before {
    position: absolute;
    top: 25px;
    right: 30px;
    width: 60px;
    height: 60px;
    background-repeat: no-repeat;
    background-position: center;
    opacity: 0.5;

    @media (min-width: @tablet-width) {
      top: 35px;
      right: 30px;
    }

    @media (min-width: @desktop-width) {
      top: 35px;
      right: 33px;
    }
  }

  &:hover {
    &::before {
      opacity: 0.7;
    }
  }

  &:active {
    &::before {
      opacity: 0.3;
    }
  }

  &--toys {
    background-color: @tiffany-3;

    &::before {
      content: "";
      background-image: url("../img/toy.svg");
    }

    &:hover {
      background-color: @tiffany-5;
    }

    &:active {
      background-color: @tiffany-5;
    }
  }

  &--interior {

    background-color: @tiffany-2;

    &::before {
      content: "";
      background-image: url("../img/interior.svg");
    }

    &:hover {
      background-color: @tiffany-4;
    }

    &:active {
      background-color: @tiffany-4;
    }
  }
}
